import { useContext, useState } from 'react'
import PropTypes from 'prop-types'

import ClearIcon from '@mui/icons-material/Clear'
import EditIcon from '@mui/icons-material/Edit'
import { IconButton, TextField } from '@mui/material'
import TodosContext from '../../api/TodosContext'

export default function Todo(props) {
  const { editTodo, deleteTodo } = useContext(TodosContext)
  const [state, setState] = useState({
    editMode: false,
    prevent: false,
    timer: null
  })

  const [tempData, setTempData] = useState(null)

  const enterEditMode = e => {
    e.stopPropagation()
    clearTimeout(state.timer)
    setState({ ...state, editMode: true, prevent: true, timer: null })
    setTempData(props.data)
  }

  const setDone = () => {
    setState({
      ...state,
      timer: setTimeout(() => {
        if (!state.prevent) {
          setState({ ...state, prevent: false })
          editTodo({ ...props.data, done: !props.data.done })
        }
      }, 50)
    })
  }

  const handleDelete = e => {
    e.stopPropagation()
    if (!state.prevent) deleteTodo(props.data)
  }

  const handleSubmit = e => {
    if (e.keyCode !== 13) return
    editTodo(tempData)
    leaveEditMode()
  }

  const leaveEditMode = () => {
    setState({ ...state, editMode: false, prevent: false })
    setTempData(null)
  }

  const classes = 'flex flex-row justify-between w-full cursor-pointer px-1 my-0.5 items-start' + (props.data?.done ? ' bg-platinum italic line-through' : ' bg-lightcoral')

  if (state.editMode) {
    return (<div className={classes}>
      <TextField
        autoFocus={true}
        value={tempData.todo}
        onChange={e => setTempData({ ...tempData, todo: e.target.value })}
        onKeyDown={handleSubmit}
        onBlur={leaveEditMode}
      />
    </div>)
  }

  return (
    <div
      className={classes}
      draggable
      onClick={setDone}
    >
      {props.data.todo}
      {props.data.done ? (
        <IconButton size='small' onClick={handleDelete}>
          <ClearIcon fontSize='small' />
        </IconButton>
      ) : (
        <IconButton size='small' onClick={enterEditMode}>
          <EditIcon fontSize='small' />
        </IconButton>
      )}
    </div>
  )
}

Todo.propTypes = {
  data: PropTypes.object
}
