import { useState, useEffect, useContext } from 'react'
import UserContext from '../../api/UserContext'
import TodosContext from '../../api/TodosContext'
import { weekdayNames } from '../../api/Constants'
import Todo from '../../components/Todo'
import { KeyboardArrowLeftRounded, KeyboardArrowRightRounded } from '@mui/icons-material'
import { Button } from '@mui/material'
import TodoAdd from '../../components/TodoAdd'

export default function Dashboard() {
  const { user } = useContext(UserContext)
  const { todos, setTodos, getTodos } = useContext(TodosContext)
  const [weeks, setWeeks] = useState(0)
  const [weekdays, setWeekdays] = useState([])

  useEffect(() => {
    getTodos()
  }, [setTodos, user.token])

  useEffect(() => {
    const formatDate = d => `${d.getFullYear()}-${(d.getMonth() + 1).toString().padStart(2, 0)}-${d.getDate().toString().padStart(2, 0)}`
    const wd = []
    // idk why, but this works
    for (let i = 1; i < 8; i++) {
      const current = new Date((new Date()).setHours(weeks * 168))
      const dayNumber = current.getDay() || 7
      const x = i - dayNumber
      const day = new Date(current.setDate(current.getDate() + x))
      wd.push({
        category: formatDate(day),
        todos: []
      })
    }
    wd.forEach(weekday => {
      weekday.todos = []
      todos.filter(todo => todo.category === weekday.category).forEach(todo => weekday.todos.push(todo))
    })
    setWeekdays(wd)
  }, [weeks, todos])

  const backHandler = () => {
    setWeeks(weeks => weeks - 1)
  }

  const forwardHandler = () => {
    setWeeks(weeks => weeks + 1)
  }

  const weekdays2 = weekdays.map((weekday, index) => {
    const todoList =
      weekday.todos.length > 0 &&
      weekday.todos.map(todo => {
        return <Todo data={todo} key={window.crypto.randomUUID()} />
      })
    const cat = new Date(weekday.category)
    return (
      <div className='flex flex-col border-2 border-red p-1 m-1 w-[14%]' key={index}>
        <h2 className='font-bold uppercase text-2xl text-center'>
          {weekdayNames[cat.getDay()]}
        </h2>
        <p className='text-center'>
          {cat.toLocaleDateString([], { year: 'numeric', month: 'long', day: 'numeric' })}
        </p>
        {todoList}
        <TodoAdd category={weekday.category} />
      </div>
    )
  })

  return (
    <div className='flex flex-row justify-between w-full'>
      <Button onClick={backHandler}>
        <KeyboardArrowLeftRounded />
      </Button>
      {weekdays2}
      <Button onClick={forwardHandler}>
        <KeyboardArrowRightRounded />
      </Button>
    </div>
  )
}
