import { useContext } from 'react'
import { Link } from 'react-router-dom'
import UserContext from '../../api/UserContext'

const Header = () => {
  const { user } = useContext(UserContext)

  const linkClasses = 'px-2 py-2 font-bold'

  return (
    <header className='bg-red text-white p-4'>
      <Link className={linkClasses} to="/">Home</Link>
      {!user && <Link className={linkClasses} to="/signin">Sign In</Link>}
      {!user && <Link className={linkClasses} to="/signup">Sign Up</Link>}
      {user && <Link className={linkClasses} to="/dashboard">Dashboard</Link>}
      {user && <Link className={linkClasses} to="/signout">Sign Out</Link>}
    </header>
  )
}

export default Header
