import { createContext, useContext, useState } from 'react'
import PropTypes from 'prop-types'
import { todoUri } from '../Constants'
import UserContext from '../UserContext'
import CommonContext from '../CommonContext'

const TodosContext = createContext()

export const TodosProvider = props => {
  const { user } = useContext(UserContext)
  const { setLoading } = useContext(CommonContext)
  const [todos, setTodos] = useState([])

  const editTodo = async t => {
    setLoading(true)
    await fetch(`${todoUri}/edit`, {
      method: 'PUT',
      headers: {
        'Content-Type': 'application/json',
        'auth-token': user.token
      },
      body: JSON.stringify({ ...t, id: t._id })
    })
      .then(() => setLoading(false))
    getTodos()
  }

  const getTodos = async () => {
    setLoading(true)
    await fetch(`${todoUri}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'auth-token': user.token
      }
    })
      .then(res => res.json())
      .then(t => setTodos(t))
    setLoading(false)
  }

  const addTodo = async t => {
    setLoading(true)
    await fetch(`${todoUri}/add`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
        'auth-token': user.token
      },
      body: JSON.stringify(t)
    })
    setLoading(false)
    getTodos()
  }

  const deleteTodo = async t => {
    setLoading(true)
    await fetch(`${todoUri}/delete`, {
      method: 'DELETE',
      headers: {
        'Content-Type': 'application/json',
        'auth-token': user.token
      },
      body: JSON.stringify({ id: t._id })
    })
    setLoading(false)
    getTodos()
  }

  return (
    <TodosContext.Provider value={{ todos, setTodos, editTodo, getTodos, addTodo, deleteTodo }}>
      {props.children}
    </TodosContext.Provider>
  )
}

TodosProvider.propTypes = {
  children: PropTypes.node
}

export default TodosContext
