import { useContext, useEffect } from 'react'
import { HashRouter, Routes, Route, Navigate } from 'react-router-dom'

import { authUri } from './api/Constants'
import Header from './components/Header'
import Home from './pages/Home'
import Dashboard from './pages/Dashboard'
import SignUp from './pages/SignUp'
import SignIn from './pages/SignIn'
import UserContext from './api/UserContext'
import SignOut from './pages/SignOut'

export default function App() {
  const { user, setUser } = useContext(UserContext)

  useEffect(() => {
    (async () => {
      let token = localStorage.getItem('auth-token')
      if (token === null) {
        localStorage.setItem('auth-token', '')
        token = ''
      }

      const tokenResponse = await fetch(`${authUri}/token`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'auth-token': token
        }
      }).then(res => res.json())

      if (tokenResponse) {
        const userResponse = await fetch(`${authUri}/`, {
          headers: {
            'Content-Type': 'application/json',
            'auth-token': token
          }
        }).then(res => res.json())
        setUser({ token: token, user: userResponse })
      }
    })()
  }, [setUser])

  return (
    <HashRouter>
      <Header />
      <Routes>
        <Route path="/" element={<Home />} />
        <Route
          path="/dashboard"
          element={
            user ? <Dashboard /> : <Navigate to="/signin" />
          }
        />
        <Route
          path="/signup"
          element={
            !user ? <SignUp /> : <Navigate to="/dashboard" />
          }
        />
        <Route
          path="/signin"
          element={
            !user ? <SignIn /> : <Navigate to="/dashboard" />
          }
        />
        <Route
          path='/signout'
          element={
            user ? <SignOut /> : <Navigate to="/signin" />
          }
        />
      </Routes>
    </HashRouter>
  )
}
