import { useState, useContext } from 'react'
import { Link, useNavigate } from 'react-router-dom'

import UserContext from '../../api/UserContext'
import { authUri } from '../../api/Constants'
import { SubmitButton } from '../../components/StyledComponents'
import { TextField } from '@mui/material'

export default function SignIn() {
  const [formState, setFormState] = useState({
    email: '',
    password: '',
  })
  const [error, setError] = useState()

  const { setUser } = useContext(UserContext)
  const history = useNavigate()

  const handleChange = ({
    target: { id, value },
  }) =>
    setFormState(prev => {
      return { ...prev, [id]: value }
    })

  async function onSubmit(e) {
    e.preventDefault()

    const response = await fetch(`${authUri}/login`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(formState)
    }).then(res => res.json())

    if (response.msg) {
      return setError(response.msg)
    }

    setUser({
      token: response.token,
      user: response.user
    })

    localStorage.setItem('auth-token', response.token)
    history('/dashboard')
  }

  return (
    <div className='text-center max-w-[30rem] mx-auto'>
      <h1 className='text-5xl uppercase font-bold my-8'>Sign in</h1>
      <form onSubmit={onSubmit}>
        <TextField
          id='email'
          label='email'
          type='email'
          onChange={handleChange}
          variant='filled'
          error={error}
          helperText={error}
          margin='dense'
          fullWidth
        />
        <br />
        <TextField
          id='password'
          label='password'
          type='password'
          onChange={handleChange}
          variant='filled'
          error={error}
          helperText={error}
          margin='dense'
          fullWidth
        />
        <SubmitButton value="Sign in" />
      </form>
      Forgot your password? <Link to='/forgotpassword'>Click here</Link>
      <br />
      Don&apos;t have an account? <Link to="/signup">Sign up</Link>
    </div>
  )
}
