import { useContext, useState } from 'react'
import { Link, useNavigate } from 'react-router-dom'

import UserContext from '../../api/UserContext'
import { authUri } from '../../api/Constants'
import ErrorNotice from '../../components/ErrorNotice'
import { SubmitButton } from '../../components/StyledComponents'
import Field from '../../components/Field'

export default function SignUp() {
  const [formState, setFormState] = useState({
    email: '',
    password: '',
  })
  const [error, setError] = useState()

  const { setUser } = useContext(UserContext)
  const history = useNavigate()

  const handleChange = ({
    target: { id, value },
  }) =>
    setFormState(prev => {
      return { ...prev, [id]: value }
    })

  const onSubmit = async e => {
    e.preventDefault()

    const responseRegister = await fetch(`${authUri}/register`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(formState)
    }).then(res => res.json())

    if (responseRegister.msg) {
      return setError(responseRegister.msg)
    }

    const responseLogin = await fetch(`${authUri}/login`, {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: JSON.stringify(formState)
    }).then(res => res.json())

    setUser({
      token: responseLogin.token,
      user: responseLogin.user
    })

    localStorage.setItem('auth-token', responseLogin.token)
    history('/dashboard')
  }

  return (
    <div className='text-center max-w-[30rem] mx-auto'>
      <h1 className='text-5xl uppercase font-bold my-8'>Sign up for free</h1>
      {error && (
        <ErrorNotice message={error} clearError={() => setError(undefined)} />
      )}
      <form onSubmit={onSubmit}>
        <Field
          id='email'
          label='email'
          type='email'
          onChange={handleChange}
        />
        <Field
          id='password'
          label='password'
          type='password'
          onChange={handleChange}
        />
        <SubmitButton value="Sign Up" />
      </form>
      Already have an account? <Link to="/signin">Sign In</Link>
    </div>
  )
}
