import PropTypes from 'prop-types'

export default function ErrorNotice(props) {
  return (
    <div className='bg-platinum rounded-lg py-1'>
      <span className='text-red font-bold'>{props.message}</span>&nbsp;
      <button
        className='bg-black text-platinum px-2 rounded-xl'
        onClick={props.clearError}
      >X</button>
    </div>
  )
}

ErrorNotice.propTypes = {
  message: PropTypes.string,
  clearError: PropTypes.func
}
