import { useState, useContext } from 'react'
import PropTypes from 'prop-types'
import { TextField } from '@mui/material'

import TodosContext from '../../api/TodosContext'

export default function TodoAdd({ category }) {
  const initialState = {
    displayText: false,
    todo: ''
  }
  const [state, setState] = useState(initialState)

  const { addTodo } = useContext(TodosContext)

  const displayText = () => {
    setState(state => ({ ...state, displayText: true }))
  }

  const handleChange = e => {
    setState(state => ({ ...state, todo: e.target.value }))
  }

  const handleSubmit = e => {
    if (e.keyCode !== 13) return

    const todo = {
      todo: state.todo,
      category: category
    }
  
    addTodo(todo)
    setState(initialState)
  }

  const handleLeave = () => {
    if (state.todo.trim() !== '') {
      const todo = {
        todo: state.todo,
        category: category
      }
      addTodo(todo)
    }
    setState(initialState)
  }

  const classes = 'w-full px-1 my-0.5 bg-platinum cursor-text'
  return (
    <div
      className={classes}
      onClick={() => !state.displayText && displayText()}
    >
      {state.displayText ? <TextField autoFocus={true} value={state.todo} onChange={handleChange} onKeyDown={handleSubmit} onBlur={handleLeave} /> : <>&nbsp;</>}
    </div>
  )
}

TodoAdd.propTypes = {
  category: PropTypes.string
}
