import PropTypes from 'prop-types'

export default function Field({ id, label, type, onChange }) {
  return (
    <div className='
      border border-platinum rounded
      text-left flex flex-col
      mx-0 my-4 px-4 py-2
    '>
      <label
        className='text-[11px]'
        htmlFor={id}
      >{label}</label>
      <input
        className='h-full text-lg outline-none'
        type={type}
        id={id}
        onChange={onChange}
      />
    </div>
  )
}

Field.propTypes = {
  id: PropTypes.string,
  label: PropTypes.string,
  type: PropTypes.string,
  onChange: PropTypes.func
}
